":["\n$header-icon-size: 20px;\n\n.app-menu {\n  width: 100%;\n  display: flex;\n  flex-shrink: 1;\n  flex-wrap: wrap;\n}\n\n.app-menu-main {\n  display: flex;\n  flex-wrap: nowrap;\n\n  .app-menu-entry {\n    width: 50px;\n    height: 50px;\n    position: relative;\n    display: flex;\n\n    &.app-menu-entry__active {\n      opacity: 1;\n\n      &::before {\n        content: \" \";\n        position: absolute;\n        pointer-events: none;\n        border-bottom-color: var(--color-main-background);\n        transform: translateX(-50%);\n        width: 12px;\n        height: 5px;\n        border-radius: 3px;\n        background-color: var(--color-primary-text);\n        left: 50%;\n        bottom: 6px;\n        display: block;\n        transition: all 0.1s ease-in-out;\n        opacity: 1;\n      }\n\n      .app-menu-entry--label {\n        font-weight: bold;\n      }\n    }\n\n    a {\n      width: calc(100% - 4px);\n      height: calc(100% - 4px);\n      margin: 2px;\n      color: var(--color-primary-text);\n      position: relative;\n    }\n\n    img {\n      transition: margin 0.1s ease-in-out;\n      width: $header-icon-size;\n      height: $header-icon-size;\n      padding: calc((100% - $header-icon-size) / 2);\n      box-sizing: content-box;\n      filter: var(--background-image-invert-if-bright, var(--primary-invert-if-bright));\n    }\n\n    .app-menu-entry--label {\n      opacity: 0;\n      position: absolute;\n      font-size: 12px;\n      color: var(--color-primary-text);\n      text-align: center;\n      left: 50%;\n      top: 45%;\n      display: block;\n      min-width: 100%;\n      transform: translateX(-50%);\n      transition: all 0.1s ease-in-out;\n      width: 100%;\n      text-overflow: ellipsis;\n      overflow: hidden;\n      letter-spacing: -0.5px;\n    }\n\n    &:not(.app-menu-entry__hidden-label):not(.app-menu-entry__show-hovered):hover,\n    &:not(.app-menu-entry__hidden-label):not(.app-menu-entry__show-hovered):focus-within {\n      opacity: 1;\n      .app-menu-entry--label {\n        opacity: 1;\n        font-weight: bolder;\n        bottom: 0;\n        width: 100%;\n        text-overflow: ellipsis;\n        overflow: hidden;\n      }\n    }\n  }\n\n  // Show labels\n  &:hover,\n  &:focus-within,\n  .app-menu-entry:hover,\n  .app-menu-entry:focus {\n    opacity: 1;\n  }\n\n  &:not(.app-menu-main__hidden-label):not(.app-menu-main__show-hovered):hover,\n  &:not(.app-menu-main__hidden-label):not(.app-menu-main__show-hovered):focus-within,\n  .app-menu-entry:not(.app-menu-entry__hidden-label):hover,\n  .app-menu-entry:not(.app-menu-entry__hidden-label):focus {\n    opacity: 1;\n\n    img {\n      margin-top: -8px;\n    }\n\n    .app-menu-entry--label {\n      opacity: 1;\n      bottom: 0;\n    }\n\n    &::before, .app-menu-entry::before {\n      opacity: 0;\n    }\n  }\n\n  &.app-menu-main__show-hovered .app-menu-entry:hover,\n  &.app-menu-main__show-hovered .app-menu-entry:focus {\n    img {\n      margin-top: -8px;\n    }\n\n    .app-menu-entry--label {\n      opacity: 1;\n      bottom: 0;\n    }\n\n    &::before, .app-menu-entry::before {\n      opacity: 0;\n    }\n  }\n}\n\n::v-deep .app-menu-more .button-vue--vue-tertiary {\n  opacity: .7;\n  margin: 3px;\n  filter: var(--background-image-invert-if-bright, var(--primary-invert-if-bright));\n\n  &:not([aria-expanded=\"true\"]) {\n    color: var(--color-primary-element-text);\n\n    &:hover {\n      opacity: 1;\n      background-color: transparent !important;\n    }\n  }\n\n    &:focus-visible {\n      opacity: 1;\n      outline: none !important;\n    }\n}\n\n.app-menu-popover-entry {\n  .app-icon {\n    position: relative;\n    height: 44px;\n    width: 48px;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    filter: var(--background-invert-if-bright, var(--primary-invert-if-bright));\n\n    &.has-unread::after {\n      background-color: var(--color-main-text);\n    }\n\n    img {\n      width: $header-icon-size;\n      height: $header-icon-size;\n    }\n  }\n}\n\n.has-unread::after {\n  content: \"\";\n  width: 8px;\n  height: 8px;\n  background-color: var(--color-primary-element-text);\n  border-radius: 50%;\n  position: absolute;\n  display: block;\n  top: 10px;\n  right: 10px;\n}\n\n.unread-counter {\n  display: none;\n}\n"],"sourceRoot":""}]);
